@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&family=Roboto+Condensed:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(#303b37, #1a1f1d);
  color: #e1eeeb;
  min-height: 100vh;
}

#header {
  text-align: center;
  margin: 3rem auto;
}

#header img {
  width: 20rem;
  height: 20rem;
  object-fit: contain;
  background-color: transparent;
}

#header h1 {
  font-size: 1.5rem;
}

#user-input {
  padding: 1.5rem;
  max-width: 80rem;
  margin: 2rem auto;
  border-radius: 8px;
  background: linear-gradient(180deg, #0dffc7, #2b996d);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  gap: 1.5rem;
}

.input-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

#user-input label {
  display: block;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #123b2b;
  margin-bottom: 0.3rem;
  text-align: left;
}

#user-input input {
  width: 10rem;
  padding: 0.6rem 0.8rem;
  border: 1px solid #76c0ae;
  border-radius: 0.4rem;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.2s ease;
}

#user-input input:focus {
  outline: none;
  border-color: #0dffc7;
  background-color: rgba(255, 255, 255, 0.2);
}

#user-input button {
  background-color: #1a1f1d;
  color: wheat;
  font-size: 1.1rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  align-self: center;
}

#user-input button:hover {
  background-color: #2a2f2d;
  transform: scale(1.05);
}

#result {
  max-width: 50rem;
  margin: 2rem auto;
  padding: 1rem;
  table-layout: fixed;
  border-spacing: 1rem;
  text-align: right;
}

#result thead {
  font-size: 0.7rem;
  color: #83e6c0;
}

#result p {
  text-align: center;
}

#result span {
  font-weight: bold;
}

#result tbody {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 0.85rem;
  color: #c2e9e0;
}

.center {
  font-size: 90%;
  text-align: center;
  text-decoration: overline;
}

#won {
  color: green;
}

#lost {
  color: red;
}
